import './App.css';
import React, { useState, useEffect } from 'react';
import Content from './components/content';
import Header from './components/header'
import Social from './components/social'
import About from './components/about_me'
import './switcher.scss'
// import Responsive from './window'


function App() {

  const [currTheme, setTheme] = useState('theme-blue');

  useEffect(()=>{
    const currentThemeColor = localStorage.getItem('theme-color');
    if(currentThemeColor)
      setTheme(currentThemeColor)
  }, [])


  const handleClick = (theme)=>{
    setTheme(theme)
    localStorage.setItem('theme-color', theme)
  }

  return (
    <div className = "hi">
    <div className= {`App ${currTheme}`}>
      <div className = "theme-box">
      <p>Change your theme here!</p>

      <div id = "theme-black" className={`${currTheme === 'theme-black' ? 'active' : ''}`}
        onClick = {()=> handleClick('theme-black')}
        ></div>
        <div id = "theme-blue" className={`${currTheme === 'theme-blue' ? 'active' : ''}`}
        onClick = {()=> handleClick('theme-blue')}
        ></div>
        <div id = "theme-red" className={`${currTheme === 'theme-red' ? 'active' : ''}`}
        onClick = {()=> handleClick('theme-red')}></div>
        <div id = "theme-green" className={`${currTheme === 'theme-green' ? 'active' : ''}`}
        onClick = {()=> handleClick('theme-green')}></div>
        <div id = "theme-pink" className={`${currTheme === 'theme-pink' ? 'active' : ''}`}
        onClick = {()=> handleClick('theme-pink')}></div>

      </div>
      <Header/>
      <Content/>
      <About/>
      <Social/>
    </div>
    </div>
  );
}

export default App;
